import notList from "@/components/notList.vue";
import UserAttention from "./userAttention-model.js";
const userAttention = new UserAttention();
export default {
	components: {
	  notList
	},
	data() {
		return {
			attentionNode: {
				data: []
			}
		};
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.getList(() => {
				callBack && callBack();
			})
		},
		getList(callBack) {
			const that = this
			userAttention.getList({
				openid: that.userInfo.openid	
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					this.attentionNode = res
				}
				callBack && callBack();
			})
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return userAttention.onShareAppMessage({});
	}
}
